/* BIO */

.BioContainer {

    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    text-align: center;
    margin: 0 auto;
    align-items: center;
    margin-bottom: 10px;
    padding-bottom: 20px;
    padding-top: 20px;
    border-bottom: 2px dotted rgb(43,29,40);

}

.AvatarImg {

    height: 250px;
    width: auto;
    border: 5px solid white;
    background-color: rgba(255, 255, 255, 0.288);
    backdrop-filter: blur( 4px );
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    align-self: center;
    text-align: center;

}

.AvatarContainer {

    display: flex;
    width: 100%;
    overflow: hidden;
    margin-bottom: 10px;
    background-color: rgb(209, 209, 209);

}

.AvatarContainer div {

    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;

}

.AvatarContainer h1 {

    font-size: 120px;
    margin: 0px;
    padding: 0px;

}

.AvatarContainer h1.FirstName {

    color: white;
    text-align: left;

}

.AvatarContainer h1.LastName {

    color: black;
    text-align: right;

}

.BioContent {

    display: flex;
    justify-content: space-between;
    text-align: left;
    text-indent: 25px;
    font-size: 17px;
    width: fit-content;
    padding: 10px 10px 0px 10px;

}

.Description {

    display: flex;
    justify-content: center;
    align-items: center;

}

/* MOBILE MEDIA QUERIE */

@media only screen 
  and (min-width: 300px) 
  and (max-width: 812px) {

    .BioContainer {

        flex-direction: column;
        justify-content: center;
        align-items: center;

    }

    .BioContent {

        display: flex;
        justify-content: flex-start;
        padding: 15px;
        text-align: left;
        text-indent: 25px;
        font-size: 15px;
        width: 90%;
        margin: 0 auto;
        padding-top: 20px;
        font-size: 1.1rem;

    }

    .AvatarContainer {

        min-height: 450px;

    }

    .AvatarContainer div {

        justify-content: space-between;
        align-items: center;
        /* flex-direction: row; */

    }

    .AvatarContainer h1 {

        font-size: 5rem;
        /* z-index: 10; */
    
    }

    h1.FirstName {
        z-index: 10;
    }


}